import React, { PureComponent } from 'react';
import { withRouter } from 'react-router-dom';
import Toast from '../../components/Toast/Toast';
import BaiHatService from './BaiHatService';
import backgroundImage from '../../assets/images/background.jpg';
import spinImage from '../../assets/images/spin.jpg';
import BaiHatTable from './BaiHatTable';
import Player from './Player';
import Setting from './Setting';
import LeftPlayground from './LeftPlayground';
import { NO_LYRIC, FETCHING_LYRIC, LYRIC_NOT_FOUND, THEME_LIGHT } from '../../constants/Constants';
import {
  getById,
  saveSetting,
  getSetting,
  decodeTime,
  createNewElement,
  myParseFloat,
  getByClass,
  filterTable,
  getAbsoluteUrl,
  isValidDate,
  formatDateMY
} from '../../service/UtilService';
import { scrollPage, scrollInsideElementToCenter } from '../../service/scroll';
import { LiliContext } from '../../context/LiliContext';
import './BaiHat.css';
import { buildDate } from '../../buildInfo';

class BaiHat extends PureComponent {
  static contextType = LiliContext;

  constructor(props) {
    super(props);
    let flipWaveform = getSetting('flipWaveform');
    if (flipWaveform === undefined || flipWaveform === null) {
      flipWaveform = false;
    }
    this.state = {
      songData: [],
      params: {
        page: 0
      },
      analyser: null,
      loading: false,
      song: {}, // current song
      showSetting: false,
      isFullscreen: !!getSetting('fullscreen'), // !!null = false
      showWaveform: getSetting('showWaveform') || false,
      flipWaveform: flipWaveform,
      isPlaying: false,
      spinning: false
    };

    this.lines = [];
    this.isLrc = false;
    this.hasTranslation = false;
    this.startTimes = [];
    this.endTimes = [];
    this.cntWord = 0;
    this.offsetTime = 0;
    this.currWordID = -1;
    this.metaExist = false;
    this.lyricMeta = {};
    this.myAudio = null;
    this.playLyricInterval = null;
    this.countdownInterval = null;
    this.currErrWord = null;
    this.playerInstance = null;
    this.waveformInstance = null;
    this.initSettingsAtPlayer = null;
    this.div_result = React.createRef();
    this.lyric_playground = React.createRef();
    this.div_background = React.createRef();
    this.div_left_wrapper = React.createRef();
  }

  componentDidMount() {
    const sortBy = getSetting('sortBy');
    const sortOrder = getSetting('sortOrder');
    this.getAllSongs(
      {
        sortBy,
        sortOrder
      },
      true
    );

    // tham chiếu tới myAudio của component con, thực ra có thể dùng:
    // this.myAudio = this.playerInstance.myAudio;
    // nhưng muốn dùng cả 2 cách nên để như vậy
    // (playerInstance chỉ dùng để call các function trong component Player)
    this.myAudio = this.child.myAudio;

    if (this.state.isFullscreen) {
      this.setFullscreen();
    }

    window.addEventListener('keydown', (e) => {
      if (!this.myAudio) return;

      switch (e.key) {
        case 'ArrowLeft':
          this.myAudio.currentTime -= 5;
          this.onSeeked();
          break;
        case 'ArrowRight':
          this.myAudio.currentTime += 5;
          this.onSeeked();
          break;
        default:
          break;
      }
    });
  }

  componentWillUnmount() {
    this.clearPlayLyricInterval();
    this.clearCountdownInterval();
  }

  initAnalyzer = () => {
    if (this.state.analyser) return;
    const audioContext = new (window.AudioContext || window.webkitAudioContext)(); // Safari uses webkitAudioContext
    const audioSrcNode = audioContext.createMediaElementSource(this.myAudio);
    const analyser = audioContext.createAnalyser();
    audioSrcNode.connect(analyser);
    analyser.connect(audioContext.destination);
    analyser.fftSize = 128;
    this.setState({
      analyser
    });
  };

  getAllSongs = (params, isPlaySong = false) => {
    this.setState({ loading: true });
    const sort = params.sortBy ? params.sortBy + ',' + params.sortOrder : this.state.params.sort;
    if (params.sortBy) {
      saveSetting('sortBy', params.sortBy);
      saveSetting('sortOrder', params.sortOrder);
    }
    const newParams = {
      ...this.state.params,
      ...params,
      sort
    };
    this.setState({
      params: newParams
    });
    BaiHatService.getAllSongs(newParams)
      .then((res) => {
        const path = this.props.match.params.id;
        let song = {};
        let currInx = 0;
        if (path) {
          song = res.find((obj, idx) => {
            if (obj.path === path) {
              currInx = idx;
              return true;
            }
            return false;
          });
        }

        this.setState({
          songData: res,
          loading: false
        });
        this.playerInstance.setPlayList(res, currInx); // danh sách phát và 1 bài trong danh sách đó muốn được phát ngay lúc này
        if (isPlaySong && song.path) {
          this.playSong(song);
        }
        this.scrollTableToActiveSong(song);
      })
      .catch((err) => {
        console.log(err);
        Toast.error(err);
        this.setState({
          loading: false
        });
      });
  };

  /**
   * Play a song
   * @param {JSON} song info of a song we want to play
   */
  playSong = (song, isScrollTr = false) => {
    console.log(`Start playing ${song.title} - ${song.artist}...`);
    if (this.props.match.params.id !== song.path) {
      window.location.href = '#/bai-hat/' + song.path;
    }

    if (song.zing_id) {
      song.zingStream = getAbsoluteUrl('/api/zing/mp3/streaming?zing_id=' + song.zing_id);
    }

    // [js_async] cần chờ setState xong để nó render xong đã, sau đó mới fetch lyric,
    // bởi vì trong getLyric, nếu có lyric, sẽ chờ hàm BaiHatService.getLyric
    // thực hiện xong mới gọi saveLyric, còn khi ko có lyric sẽ gọi luôn hàm setNoLyric.
    // 2 trường họp đó khác nhau dẫn đến kq khác nhau (có lyric thì ok, ko có thì lỗi)!
    this.setState({ song }, () => {
      this.getLyric(song);
    });
    const image_url = song && song.image_url ? getAbsoluteUrl(song.image_url) : spinImage;
    this.playerInstance.setActiveAudio(song);
    this.playerInstance.setMediaSessionMeta(song.title, song.artist, image_url);

    if (isScrollTr) {
      this.scrollTableToActiveSong(song);
    }
  };

  scrollTableToActiveSong = (song = {}) => {
    const songTable = getById('songTable');
    if (!songTable || !song.id) return;
    const allSongValue = songTable.parentNode;
    const activeTr = getById('tr-' + song.id);
    // allSongValue.scrollTop = activeTr.offsetTop - 34; // 34px = height of table header
    scrollInsideElementToCenter(allSongValue, activeTr, 300); // chả cần animation chỗ này đâu :v
  };

  getLyric = (song) => {
    // FetchingLyric
    this.div_result.innerHTML = FETCHING_LYRIC;
    this.lines = [];
    if (song.lyric) {
      // Currently only support .lrc, .xlrc and .trc. Will support .xtrc in the future
      // .lrc: lyric with time for each line
      // .xlrc: lrc lyric with translation
      // .trc: lyric with time for each line and word
      // .xtrc: trc lyric with translation
      if (song.lyric.endsWith('.lrc')) {
        this.isLrc = true;
        this.hasTranslation = false;
      } else if (song.lyric.endsWith('.xlrc')) {
        this.isLrc = true;
        this.hasTranslation = true;
      } else {
        this.isLrc = false;
        this.hasTranslation = false;
      }
      BaiHatService.getLyric(song.lyric)
        .then((res) => {
          if (res === LYRIC_NOT_FOUND) this.setNoLyric();
          else this.saveLyric(res);
        })
        .catch((err) => {
          console.log(err);
          Toast.error(err);
        });
    } else {
      this.setNoLyric();
    }
  };

  saveLyric = (lyric) => {
    this.lines = lyric.trim().split('\n');
    this.initLyric();
  };

  isNoLyric = () => {
    return this.lines.length === 0;
  };

  setNoLyric = () => {
    this.lines = [];
    this.initLyric();
  };

  initLyric = () => {
    this.setPlayLyricInterval();
    this.div_result.innerHTML = '';
    this.startTimes = [];
    this.endTimes = [];
    this.tranMaps = [];
    this.cntWord = 0;
    this.offsetTime = 0;
    this.currWordID = -1;
    this.playerInstance.playAudio();

    if (this.isNoLyric()) {
      this.div_result.innerHTML = NO_LYRIC;
    } else {
      this.metaExist = false;
      this.lyricMeta = {};
      this.div_result.innerHTML = '';
      if (this.isLrc) this.initLrcLyric();
      else this.initTrcLyric();
    }

    this.initSettingsAtPlayer();
  };

  initTrcLyric = async () => {
    let temp, startLine, words, startWord, endWord, divLine;

    // Note: dùng "for in" ko được, phải dùng "for await of"
    // Ref: https://developer.mozilla.org/en-US/docs/Web/JavaScript/Reference/Statements/for-await...of
    for await (let line of this.lines) {
      temp = line.match(/\[\d+:\d+\.\d+\]/g); // ex: temp = ["[1:15.047]"]
      if (temp != null) {
        startLine = decodeTime(temp[0]); // ex: 75.047
        temp = line.substring(temp[0].length); // ex: <271>I <256>keep <790>saying <1254>no
        words = temp.match(/<\d+>[^<]*/g);
        divLine = createNewElement('div', null, 'line');

        if (words == null) {
          // Trường hợp này xảy ra với các file lyric cũ của TTPod, nó có 1-2 dòng như này:
          // [99:00.100]好音质,天天动听!
          // (Ko có millisecond, chẳng hạn <123>) Tốt nhất là ignore
          continue;
        }

        startWord = startLine;
        for await (let word of words) {
          // match(/\d+/)[0]: lấy số đầu tiên trong string này
          // VD: string này có dạng: "<123>demo " thì kết quả là 123
          // VD: string này có dạng: "<123>demo 949" thì kết quả là 123
          endWord = startWord + parseFloat(word.match(/\d+/)[0]) / 1000;
          endWord = myParseFloat(endWord);

          // Do lưu time vào 2 mảng startTimes và endTimes nên việc
          // thêm attribute time-start và time-start chỉ dùng để debug
          let spanWord = createNewElement('span', 'word-' + this.cntWord, 'not-pass-word', {
            'time-start': startWord,
            'time-end': endWord
          });
          spanWord.innerText = word.replace(/<\d+>/, '');
          if (spanWord.innerText.trim() === '') {
            spanWord.innerHTML = '&nbsp;';
            spanWord.setAttribute('is-empty', '1');
          }
          this.startTimes[this.cntWord] = startWord;
          this.endTimes[this.cntWord] = endWord;

          startWord = endWord;
          this.cntWord++;
          divLine.appendChild(spanWord);
        }
        this.div_result.appendChild(divLine);
      } else {
        this.readLyricMeta(line);
      }
    }

    this.appendLyricMeta();
  };

  initLrcLyric = async () => {
    let lineTime = '',
      lineTimeLen,
      lineTranTag,
      tranNum,
      lineTranText,
      lineWords,
      startLine;
    let lineMap = new Map(); // key là 1 phần tử trong mảng startTimes

    // Map<tranNum, Map<startLine, lineTranText>>
    let lineTranMap = new Map();

    // 1. Parse lyric

    // ex: line = "[01:53.41][00:28.13]Người theo hương hoa "
    for await (let line of this.lines) {
      // lineTime có thể có 1 hoặc nhiều phần tử, ex: lineTime = ['[01:53.41]', '[00:28.13]']
      lineTime = line.match(/\[\d+:\d+\.\d+\]/g);

      // Dòng có lineTranTag là dòng dịch, có thể có nhiều loại ngôn ngữ khác nhau, ex:
      // [tran1]Wǒmen táitóu wàng tiānkōng
      // [tran2]We raise our heads to the sky
      // ex: lineTranTag = ['[tran1]'], should have only 1 element
      lineTranTag = line.match(/\[tran\d+\]/g);

      if (lineTime != null) {
        // ex: lineTimeLen = length của [01:53.41][00:28.13] = 20
        lineTimeLen = lineTime.reduce((total, curr) => total + curr.length, 0);
        lineWords = line.substring(lineTimeLen); // ex: "Người theo hương hoa "

        for await (const lineT of lineTime) {
          startLine = decodeTime(lineT); // ex: startLine = decodeTime('[01:53.41]') = 113.41
          this.startTimes[this.cntWord] = startLine;

          // ex: lineMap = { 113.41 => "Người theo hương hoa ", 28.13 => "Người theo hương hoa " }
          lineMap.set(startLine, lineWords);

          this.cntWord++;
        }
      } else if (lineTranTag != null) {
        // Vì lineTranTag bắt buộc phải theo sau lineTime nên ta có thể dùng startLine để làm key cho mỗi lineTranTag
        tranNum = lineTranTag[0].substring(1, lineTranTag[0].length - 1); // ex: tranNum = 'tran1'
        lineTranText = line.substring(lineTranTag[0].length);
        if (!lineTranMap.has(tranNum)) {
          lineTranMap.set(tranNum, new Map());
        }
        lineTranMap.get(tranNum).set(startLine, lineTranText);
      } else {
        this.readLyricMeta(line);
      }
    }

    this.startTimes.sort(function (a, b) {
      return a - b;
    });

    // 2. Generate lyric (HTML) to display

    for (let i = 0; i < this.startTimes.length; i++) {
      let divLine = createNewElement('div', null, 'line');

      let spanWord = createNewElement('span', 'word-' + i, 'not-pass-word', null);
      spanWord.innerText = lineMap.get(this.startTimes[i]);
      if (spanWord.innerText === '') {
        spanWord.innerHTML = '&nbsp';
        spanWord.setAttribute('is-empty', '1');
      }
      divLine.appendChild(spanWord);

      // Add translation
      if (lineTranMap.size > 0) {
        for (let [key, value] of lineTranMap) {
          if (value.has(this.startTimes[i])) {
            let spanTran = createNewElement('div', null, 'line-tran ' + key, null);
            spanTran.innerText = value.get(this.startTimes[i]);
            divLine.appendChild(spanTran);
          }
        }
      }

      this.div_result.appendChild(divLine);
    }
    this.appendLyricMeta();

    for (let i = 0; i < this.startTimes.length - 1; i++) {
      this.endTimes[i] = this.startTimes[i + 1];
    }
    this.endTimes[this.startTimes.length - 1] = this.myAudio.duration;
  };

  // Note: có trường hợp 1 dòng meta có ký tự đặc biệt, chẳng hạn: '[by: Tuzaku]\r'
  // Lúc này substring sẽ bị bug: this.lyricMeta.by = "Tuzaku]"
  // Cái bug đó ko nhiều, kệ, khỏi fix! => Đã fix
  readLyricMeta = (line = '') => {
    const end = line.lastIndexOf(']');
    if (line.includes('offset:')) {
      // ex: [offset:-200]
      this.offsetTime = line.match(/[0-9|-]+/g);
      this.offsetTime = parseInt(this.offsetTime);
    } else if (line.includes('[author:')) {
      // ex: [author:Tuzaku]
      // deprecated, lyric file should use [by:] instead
      this.metaExist = true;
      this.lyricMeta.by = line.substring('[author:'.length, end).trim();
    } else if (line.includes('[by:')) {
      // ex: [by:Tuzaku]
      this.metaExist = true;
      this.lyricMeta.by = line.substring('[by:'.length, end).trim();
    } else if (line.includes('[createdDate:')) {
      // ex: [createdDate:2021-06-28]
      // deprecated, lyric file should use [date:] instead
      this.metaExist = true;
      this.lyricMeta.date = line.substring('[createdDate:'.length, end).trim();
    } else if (line.includes('[date:')) {
      // ex: [date:2021-06-28]
      this.metaExist = true;
      this.lyricMeta.date = line.substring('[date:'.length, end).trim();
    } else if (line.includes('[source:')) {
      // ex: [date:2021-06-28]
      this.metaExist = true;
      this.lyricMeta.source = line.substring('[source:'.length, end).trim();
    } else if (line.includes('[ti:')) {
      // ex: [date:2021-06-28]
      this.metaExist = true;
      this.lyricMeta.title = line.substring('[ti:'.length, end).trim();
    } else if (line.includes('[ar:')) {
      // ex: [date:2021-06-28]
      this.metaExist = true;
      this.lyricMeta.artist = line.substring('[ar:'.length, end).trim();
    }
  };

  appendLyricMeta = () => {
    if (this.metaExist) {
      this.div_result.appendChild(createNewElement('div', null, 'line-meta')); // empty line
      let divLine = createNewElement('div', null, 'line-meta');
      divLine.innerText = '-----------';
      this.div_result.appendChild(divLine);

      const divLyricInfo = createNewElement('div', null, 'lyric-info');
      this.div_result.appendChild(divLyricInfo);

      // Song meta
      if (this.lyricMeta.title) {
        divLine = createNewElement('div', null, 'line-meta', { style: 'font-weight:bold' });
        divLine.innerText = this.lyricMeta.title + ' - ' + this.lyricMeta.artist;
        divLyricInfo.appendChild(divLine);
      }

      // Lyric meta
      if (this.lyricMeta.by) {
        divLine = createNewElement('div', null, 'line-meta');
        divLine.innerText = 'Lyric creator: ' + this.lyricMeta.by;
        divLyricInfo.appendChild(divLine);
      }
      if (this.lyricMeta.source) {
        divLine = createNewElement('div', null, 'line-meta');
        divLine.innerText = 'Source: ' + this.lyricMeta.source;
        divLyricInfo.appendChild(divLine);
      }
      if (this.lyricMeta.date) {
        const d = new Date(this.lyricMeta.date);
        const dStr = isValidDate(d) ? formatDateMY(d) : d;
        divLine = createNewElement('div', null, 'line-meta');
        divLine.innerText = 'Date: ' + dStr;
        divLyricInfo.appendChild(divLine);
      }
    }
  };

  setPlayLyricInterval = () => {
    if (this.myAudio && !this.myAudio.paused) {
      this.playLyricInterval = setInterval(() => {
        this.updateLyric();
      }, 30);
    }
  };

  clearPlayLyricInterval = () => {
    if (this.playLyricInterval) clearInterval(this.playLyricInterval);
    this.playLyricInterval = null;
  };

  onSeeked = () => {
    this.clearCountdownInterval();
    if (!this.playLyricInterval) {
      this.updateLyric();
    }
    setTimeout(() => {
      if (!this.isNoLyric()) {
        this.reUpdateWordPass();
      }
    }, 0);
    this.scrollLyric();
  };

  onPause = () => {
    this.clearCountdownInterval();
    this.clearPlayLyricInterval();
    this.setState({ isPlaying: false, spinning: false });
    if (this.waveformInstance) this.waveformInstance.stopWaveform();
  };

  onPlay = () => {
    if (!this.playLyricInterval) this.setPlayLyricInterval();
    this.setState({ isPlaying: true, spinning: true });
    if (this.myAudio.currentTime === 0) {
      const path = this.props.match.params.id;
      if (path) {
        setTimeout(() => {
          let currPath = this.props.match.params.id;
          if (currPath === path) {
            BaiHatService.updateListen(path)
              .then((res) => {
                console.log(res);
              })
              .catch((err) => {
                console.log(err);
                Toast.error(err);
              });
          }
        }, 60000); // nghe 1 bài quá 1 phút mới tính 1 lượt nghe
      }
      setTimeout(() => {
        // re-update passed words (in fact, all words are not passed words)
        if (getById('word-0') && !this.isNoLyric()) {
          for (let i = 0; i < this.cntWord; i++) {
            getById('word-' + i).classList.add('not-pass-word');
          }
        }
      }, 0);
    }
    if (this.state.showWaveform && this.waveformInstance) this.drawWave();
  };

  countdownAtEmptyWord = (currWord) => {
    let nextWord = getById('word-' + (this.currWordID + 1));
    if (nextWord) {
      let timeDiff = this.endTimes[this.currWordID] - this.startTimes[this.currWordID];
      let timeLeft = this.endTimes[this.currWordID] + this.offsetTime / 1000 - this.myAudio.currentTime;
      if ((timeDiff >= 5 && timeLeft <= 5) || (timeDiff >= 3 && timeLeft <= 3)) {
        BaiHatService.setBulletText(this.lyric_playground, currWord, Math.ceil(timeLeft));
      }
    }
  };

  updateLyric = async () => {
    if (this.isNoLyric()) {
      this.clearPlayLyricInterval();
      return;
    }

    let prevWord, prevParent;

    // Chạy hiệu ứng karaoke cho từ hiện tại
    if (this.currWordID >= 0) {
      const st = this.startTimes[this.currWordID] + this.offsetTime / 1000;
      const en = this.endTimes[this.currWordID] + this.offsetTime / 1000;
      const currWord = getById('word-' + this.currWordID);

      if (st <= this.myAudio.currentTime && en >= this.myAudio.currentTime) {
        // TODO change this to state variable, or class variable (karaokeMode)
        if (this.div_result.classList.contains('karaoke-mode')) {
          BaiHatService.runKaraokeEffect(this.lyric_playground, this.myAudio, currWord, st, en);
        }
        if (currWord.getAttribute('is-empty') === '1') {
          this.countdownAtEmptyWord(currWord);
        }

        return;
      }

      // Sau khi myAudio.currentTime nằm ngoài khoảng [st:en] thì đã đi qua
      // word hiện tại, lúc này currWord sẽ trở thành preWord
      prevWord = getById('word-' + this.currWordID);
      if (prevWord && prevWord.getAttribute('is-empty') === '1') {
        // Xóa bullet text ở từ trước đó, thường thì từ đó sẽ đứng trên 1 empty line
        BaiHatService.setBulletText(this.lyric_playground, prevWord, 0);
      }

      // Có thể từ trước đó mới chỉ fill 95% chẳng hạn, ta cần fill nốt từ đó
      BaiHatService.fillKaraokeWord(this.lyric_playground, prevWord);

      prevParent = prevWord.parentNode;
      prevWord.classList.remove('word-active', 'curr-word');
    }

    // Sau khi chạy xong hiệu ứng karaoke cho từ hiện tại, word này trở thành prevWord,
    // cần tìm currWord mới, sau đó thêm các class cho currWord, currLine,
    // các từ trước và sau currWord ở trên cùng 1 line
    this.gotoNextWord(prevWord, prevParent);
  };

  gotoNextWord = async (prevWord, prevParent) => {
    this.currWordID = this.getCurrentWordByTime(this.myAudio.currentTime);
    if (this.currWordID >= 0) {
      const currWord = getById('word-' + this.currWordID);
      const currParent = currWord.parentNode;

      currWord.classList.add('word-active', 'curr-word');
      currWord.classList.remove('not-pass-word');

      if (prevParent !== currParent) {
        if (prevParent) {
          prevParent.classList.remove('line-active');
          for await (let child of prevParent.children) {
            if (child === prevWord) break;
            child.classList.remove('word-active');
          }
        }

        currParent.classList.add('line-active');
      }
      this.activeAtCurr(currWord, currParent);
    } else {
      this.removeActiveLastLine();
    }
  };

  /**
   * Active các từ trước currWord, và remove active cho các từ phía sau currWord
   * @param {HTMLElement} currWord tag này là current word
   * @param {HTMLElement} currParent tag này là cha chứa current word ở trên
   */
  activeAtCurr = (currWord, currParent) => {
    let currIndex;
    for (let i = 0; i < currParent.children.length; i++) {
      if (currParent.children[i] === currWord) {
        currIndex = i;
        break;
      }
      currParent.children[i].classList.add('word-active');
    }
    for (let i = currIndex + 1; i < currParent.children.length; i++) {
      if (!currParent.children[i].classList.contains('word-active')) break;
      currParent.children[i].classList.remove('word-active');
    }

    // scroll
    if (currWord === currParent.childNodes[0]) {
      setTimeout(() => {
        this.scrollLyric();
      }, 110); // phải chờ cho CSS transition xong mới get được height đúng
    }
  };

  /**
   * Chạy hết nhạc thì xóa active ở dòng cuối cùng
   */
  removeActiveLastLine = async () => {
    const finalLine = getById('word-' + (this.cntWord - 1)).parentNode;
    if (finalLine.classList.contains('line-active')) {
      finalLine.classList.remove('line-active');
      for await (let child of finalLine.children) {
        child.classList.remove('word-active');
      }
    }
  };

  // TODO khi hết bài cần scroll xuống cuối cùng (metadata), nếu không có info không thấy được
  scrollLyric = (currWord = null) => {
    if (currWord == null) {
      currWord = getById('word-' + this.currWordID);
    }
    if (currWord != null) {
      this.div_result.scrollTop =
        20 +
        currWord.parentNode.offsetTop -
        getById('word-0').offsetTop -
        this.div_result.offsetHeight / 2 +
        currWord.parentNode.clientHeight / 2; // height of parent of currWord (div.line.active-line)
    }
  };

  getCurrentWordByTime = (sec) => {
    let lo = 0,
      hi = this.cntWord - 1;
    let mid, st, en;

    while (lo <= hi) {
      mid = parseInt((lo + hi) / 2);
      st = this.startTimes[mid] + this.offsetTime / 1000;
      en = this.endTimes[mid] + this.offsetTime / 1000;

      if (st <= sec && en >= sec) {
        // Nếu như có 2 từ có khoảng thời gian bị chồng
        // lên nhau thì ta ưu tiên từ đứng đằng sau
        st = this.startTimes[mid + 1] + this.offsetTime / 1000;
        en = this.endTimes[mid + 1] + this.offsetTime / 1000;
        if (st <= sec && en >= sec) return mid + 1;
        else return mid;
      } else if (st > sec) hi = mid - 1;
      else lo = mid + 1;
    }

    if (mid === 0 || mid === this.cntWord - 1) mid = -1;
    else {
      // ko tìm thấy thẻ nào có st <= sec <= en (do lyric bị sai).
      // tuy vậy, vẫn return thẻ gần đó nhất
      // if(mid)
      if (mid && mid !== this.currErrWord) {
        Toast.warn(`Warning: lyric is not correct near "${getById('word-' + mid).innerText}"`);
        this.currErrWord = mid;
      }
    }

    // delete interval when finish song!
    if (sec > this.endTimes[this.cntWord - 1]) this.clearPlayLyricInterval();

    return mid;
  };

  clearCountdownInterval = () => {
    clearInterval(this.countdownInterval);
    this.countdownInterval = null;
  };

  reUpdateWordPass = () => {
    // this.currWordID giờ sẽ là vị trí mới sau thao tác seek nhảy tới
    this.currWordID = this.getCurrentWordByTime(this.myAudio.currentTime);
    if (this.currWordID >= 0) {
      for (let i = 0; i < this.currWordID; i++) {
        let passedWord = getById('word-' + i);
        passedWord.classList.remove('not-pass-word');
        if (passedWord.getAttribute('is-empty') === '1') {
          passedWord.innerHTML = '&nbsp;'; // remove bullet text
        }
        BaiHatService.fillKaraokeWord(this.lyric_playground, passedWord);
        this.removeLineActive(passedWord);
      }
      for (let i = this.currWordID; i < this.cntWord; i++) {
        let notPassWord = getById('word-' + i);
        notPassWord.classList.add('not-pass-word');
        if (notPassWord.getAttribute('is-empty') === '1') {
          notPassWord.innerHTML = '&nbsp;'; // remove bullet text
        }
        BaiHatService.emptyKaraokeWord(notPassWord);
        this.removeLineActive(notPassWord);
      }
      // addLineActive
      getById('word-' + this.currWordID).parentNode.classList.add('line-active');
    }
  };

  removeLineActive = (child) => {
    if (child.parentNode.classList.contains('line-active')) {
      child.parentNode.classList.remove('line-active');
    }
  };

  toggleSetting = (isShow) => {
    this.setState({
      showSetting: isShow
    });
  };

  toggleFullscreen = () => {
    const isFullscreen = !this.state.isFullscreen;
    if (isFullscreen) {
      this.setFullscreen();
    } else {
      this.unsetFullscreen();
    }
    this.setState({ isFullscreen });
    saveSetting('fullscreen', isFullscreen);
    setTimeout(() => {
      this.scrollLyric();
    }, 350);
  };

  toggleWaveform = () => {
    const showWaveform = !this.state.showWaveform;
    this.setState({ showWaveform }, () => {
      if (!this.waveformInstance) return;
      if (showWaveform) this.drawWave();
      else this.waveformInstance.stopWaveform();
    });
    saveSetting('showWaveform', showWaveform);
  };

  drawWave = async () => {
    // phải đợi analyzer khởi tạo xong mới draw waveform được
    await this.initAnalyzer();
    this.waveformInstance.drawWaveform();
  };

  setFullscreen = () => {
    let body_wrapper = getByClass('body_wrapper');
    document.body.classList.add('overflow-hidden');
    body_wrapper.classList.add('body-fs');
    body_wrapper.classList.remove('body-normal');
  };

  unsetFullscreen = () => {
    let body_wrapper = getByClass('body_wrapper');
    document.body.classList.remove('overflow-hidden');
    body_wrapper.classList.remove('body-fs');
    body_wrapper.classList.add('body-normal');
    setTimeout(() => {
      scrollPage(this.lyric_playground, 300);
    }, 200);
  };

  updateSyncLyric = (offset) => {
    this.offsetTime += offset;
    Toast.success('Offset lyric: ' + this.offsetTime + 'ms');
  };

  onSearch = (obj) => {
    filterTable('songTable', 1, obj.value ? obj.value : '');
  };

  render() {
    const { songData, song, showSetting, isFullscreen, isPlaying, spinning, showWaveform, flipWaveform, analyser } = this.state;
    const { theme } = this.context;
    let songURL;
    if (song.zing_id) {
      songURL = song.zingStream;
    } else {
      songURL = song?.file_name ? getAbsoluteUrl('/api/song/file?file=' + song.file_name) : null;
    }
    const image_url = song?.image_url ? getAbsoluteUrl(song.image_url) : spinImage;
    const backgroundURL = `url(${song?.image_url ? "'" + getAbsoluteUrl(song.image_url) + "'" : backgroundImage})`;

    return (
      <div
        className={`body_wrapper ${theme === THEME_LIGHT ? 'body-light' : 'body-dark'} ${
          isFullscreen ? 'body-fs' : 'body-normal'
        }`}
      >
        <div id="all-song-wrapper">
          <div className="edit-time">Posted on: Sep 3, 2021. Last modified: {buildDate ? buildDate : 'Sep 3, 2021'}.</div>
          {songData && songData.length !== 0 && (
            <BaiHatTable
              songData={songData}
              currSong={song}
              getAllSongs={this.getAllSongs}
              playSong={this.playSong}
              onSearch={this.onSearch}
              isPlaying={isPlaying}
            />
          )}
        </div>

        <Player
          song={song}
          songURL={songURL}
          onSeeked={this.onSeeked}
          onPause={this.onPause}
          onPlay={this.onPlay}
          ref={(node) => {
            this.child = node;
          }}
          getPlayerInstance={(playerInstance) => {
            this.playerInstance = playerInstance;
          }}
          playSong={this.playSong}
        />

        <div id="lyric_playground" ref={(node) => (this.lyric_playground = node)} className="lyric-pg-picture-bg">
          <div className="background-wrapper">
            <div
              id="div_background"
              ref={(node) => (this.div_background = node)}
              className="bg-image"
              style={{ backgroundImage: backgroundURL, textAlign: 'center' }}
            ></div>
          </div>
          <div className="lyric-wrapper">
            <div id="div_left_wrapper" className="left-wrapper" ref={(node) => (this.div_left_wrapper = node)}>
              <img
                id="spinning_img"
                crossOrigin="anonymous"
                style={{ cursor: 'pointer' }}
                className={`left_img spinning ${spinning ? '' : 'paused-spin'}`}
                src={image_url}
                alt="Left info"
                onClick={() => {
                  this.setState({ spinning: !spinning });
                }}
              />
              <canvas id="spinning_canvas" style={{ display: 'none' }}></canvas>
              <LeftPlayground
                toggleWaveform={this.toggleWaveform}
                showWaveform={showWaveform}
                flipWaveform={flipWaveform}
                analyser={analyser}
                initAnalyzer={this.initAnalyzer}
                myAudio={this.myAudio}
                getWaveformInstance={(waveformInstance) => {
                  this.waveformInstance = waveformInstance;
                }}
                song={song}
              />
            </div>
            <div
              id="div_result"
              className="custom-scrollbar karaoke-mode div-res-big-active-line"
              style={{ textAlign: 'center' }}
              ref={(node) => (this.div_result = node)}
              onClick={() => this.toggleSetting(false)}
            ></div>
          </div>

          <Setting
            div_result={this.div_result}
            lyric_playground={this.lyric_playground}
            div_background={this.div_background}
            div_left_wrapper={this.div_left_wrapper}
            myAudio={this.myAudio}
            showSetting={showSetting}
            isFullscreen={isFullscreen}
            scrollLyric={this.scrollLyric}
            initSettingsAtPlayer={(func) => (this.initSettingsAtPlayer = func)}
            openSetting={() => this.toggleSetting(true)}
            updateFlipWaveform={(flip) => this.setState({ flipWaveform: flip })}
            updateSyncLyric={(offset) => this.updateSyncLyric(offset)}
          />

          <div className={`${isFullscreen ? 'setbot_fs' : ''} setting_bottom`}>
            <i className="fa fa-arrows-alt btn_align btn_fullscreen" onClick={this.toggleFullscreen}></i>
          </div>
          <div style={{ clear: 'both' }}></div>
        </div>
      </div>
    );
  }
}

export default withRouter(BaiHat);
