import React from 'react';
import Waveform from './Waveform';

const LeftPlayground = (props) => {
  const { toggleWaveform, showWaveform, flipWaveform, analyser, initAnalyzer, myAudio, getWaveformInstance, song } = props;
  return (
    <div className="left_info" onClick={toggleWaveform} title={showWaveform ? 'Switch to song meta' : 'Switch to waveform'}>
      {showWaveform && (
        <Waveform
          analyser={analyser}
          initAnalyzer={initAnalyzer}
          myAudio={myAudio}
          getWaveformInstance={getWaveformInstance}
          flipWaveform={flipWaveform}
        />
      )}
      {!showWaveform && (
        <>
          <div className="left_title">{song.title}</div>
          <div className="left_artist">{song.artist}</div>
          <div className="left_album">({song.album ? song.album : 'No Album'})</div>
        </>
      )}
    </div>
  );
};

export default LeftPlayground;
