import React from 'react';
import SearchBox from '../../components/Input/SearchBox';
import Modal from '../../components/Modal/Modal';
import { getAbsoluteUrl } from '../../service/UtilService';
import musicIcon from '../../assets/icons/music_icon.jpg';

const BaiHatTable = (props) => {
  const [showModalPicture, setShowModalPicture] = React.useState(false);
  const [selectedSong, setSelectedSong] = React.useState(null);

  const onClickPicture = (song) => {
    if (song.image_url) {
      setSelectedSong({ ...song });
      setShowModalPicture(true);
    }
  };

  const onCloseModal = () => {
    setSelectedSong(null);
    setShowModalPicture(false);
  };

  const caption = selectedSong ? selectedSong.artist + ' - ' + selectedSong.title : '';
  const rainbowColors = [
    // '#f80c12', // Lots of red, currently i don't want it
    // '#ee1100',
    '#ff3311',
    '#ff4422',
    '#ff6644',
    '#ff9933',
    '#feae2d',
    '#ccbb33',
    '#d0c310',
    '#aacc22',
    '#69d025',
    '#22ccaa',
    '#12bdb9',
    '#11aabb',
    '#4444dd',
    '#3311bb',
    '#3b0cbd',
    '#442299'
  ];
  const lastSoyBg = rainbowColors[rainbowColors.length - 1];
  const uniqueSoys = [...new Set(props.songData.map((song) => song.song_of_the_year).filter(Boolean))].sort((a, b) => b - a); // Descending: highest first
  const getSoyBackgroundColor = (soy) => {
    if (!soy) return lastSoyBg; // default color if no SOY
    const index = uniqueSoys.indexOf(soy);
    console.log('getSoyBackgroundColor', soy, index);
    return rainbowColors[index] || lastSoyBg;
  };
  const getSoyTextColor = (soy) => {
    if (!soy) return '#fff'; // default color if no SOY
    const index = uniqueSoys.indexOf(soy);
    return index < rainbowColors.length / 2 ? '#000' : '#fff';
  };

  const imgModalStyle = {
    width: '400px', // nếu browser width >= 400px thì img width cũng chỉ 400px
    maxWidth: '80%' // để resize theo with của browser nếu browser width < 400px
  };

  return (
    <div id="all_songs">
      <div className="search-wrapper">
        <SearchBox name="searchText" onSearch={props.onSearch} />
      </div>
      <div className="all-song-value custom-scrollbar">
        <table className="table_striped" id="songTable" cellSpacing="0" cellPadding="0">
          <thead>
            <tr>
              <th style={{ textAlign: 'right' }}>No.</th>
              <th
                style={{ cursor: 'pointer', minWidth: '220px' }}
                onClick={() => props.getAllSongs({ sortBy: 'title', sortOrder: 'ASC' })}
              >
                Song
              </th>
              <th style={{ cursor: 'pointer' }} onClick={() => props.getAllSongs({ sortBy: 'listens', sortOrder: 'DESC' })}>
                Listens
              </th>
              <th
                style={{ cursor: 'pointer' }}
                onClick={() => props.getAllSongs({ sortBy: 'song_of_the_year', sortOrder: 'DESC' })}
                title="Song of the year"
              >
                SOY
              </th>
              <th style={{ cursor: 'pointer' }} onClick={() => props.getAllSongs({ sortBy: 'type', sortOrder: 'ASC' })}>
                Type
              </th>
              <th style={{ cursor: 'pointer' }} onClick={() => props.getAllSongs({ sortBy: 'createdDate', sortOrder: 'DESC' })}>
                Date
              </th>
            </tr>
          </thead>
          <tbody>
            {props.songData.map((song, index) => {
              const image_url = song.image_url ? getAbsoluteUrl(song.image_url) : musicIcon;
              return (
                <tr
                  key={song.id}
                  className={props.currSong && props.currSong.path === song.path ? 'tr-active' : ''}
                  id={'tr-' + song.id}
                >
                  <td>{index + 1}</td>
                  <td className="td-song-info">
                    <img
                      className="song-picture"
                      src={image_url}
                      alt={song.artist + ' - ' + song.title}
                      onClick={() => onClickPicture(song)}
                    />
                    <a className="song-item" href={'#/bai-hat/' + song.path} onClick={() => props.playSong(song)}>
                      <div className="song-title">{song.title}</div>
                      <div className="song-artist">{song.artist}</div>
                    </a>
                    <i className={'icon-play ' + (props.isPlaying ? 'playing' : 'pause')}></i>
                  </td>
                  <td>{song.listens}</td>
                  <td>
                    {song.song_of_the_year && (
                      <span
                        className="soy"
                        style={{
                          background: getSoyBackgroundColor(song.song_of_the_year),
                          color: getSoyTextColor(song.song_of_the_year)
                        }}
                        title={`Song of the Year: ${song.song_of_the_year}`}
                      >
                        {song.song_of_the_year}
                      </span>
                    )}
                  </td>
                  <td>{song.type}</td>
                  <td>{song.formatCreatedDate}</td>
                </tr>
              );
            })}
          </tbody>
        </table>
      </div>
      {showModalPicture && (
        <Modal onClose={onCloseModal} caption={caption}>
          <img style={imgModalStyle} className="img-modal" src={getAbsoluteUrl(selectedSong.image_url)} alt={caption} />
        </Modal>
      )}
    </div>
  );
};

export default BaiHatTable;
