import axiosClient from '../../service/axiosClient';

const getAllSongs = (params) => {
  return axiosClient.get('/api/song/all', { params });
};

const getLyric = (file) => {
  return axiosClient.get('/api/lyric?file=' + file);
};

const updateListen = (path) => {
  return axiosClient.put('/api/song/listens?path=' + path);
};

const getKaraokeWordStyle = (lyric_playground, process) => {
  let isLightTheme = lyric_playground.classList.contains('lyric-pg-light');
  return (
    'background-image:' +
    //"-webkit-linear-gradient(top, rgba(255, 255, 255, 0.5) 0%, rgba(255, 255, 255, 0) 100%), " +
    '-webkit-linear-gradient(left, ' +
    (isLightTheme ? '#2199F9' : '#FC0') +
    ' ' +
    process +
    '%, ' +
    (isLightTheme ? '#333' : '#FFF') +
    ' 0%)'
  );
};

const fillKaraokeWord = (lyric_playground, word) => {
  word.setAttribute('style', getKaraokeWordStyle(lyric_playground, 100));
};

const setBulletText = (lyric_playground, emptyWord, bullet) => {
  let bulletArr = ['&nbsp;', '•', '••', '•••', '••••', '•••••'];
  emptyWord.innerHTML = bulletArr[bullet];
  fillKaraokeWord(lyric_playground, emptyWord);
};

const runKaraokeEffect = (lyric_playground, myAudio, currWord, st, en) => {
  let process = ((myAudio.currentTime - st) * 100) / (en - st);
  currWord.setAttribute('style', getKaraokeWordStyle(lyric_playground, process));
};

const emptyKaraokeWord = (word) => {
  word.setAttribute('style', '');
};

const BaiHatService = {
  getAllSongs,
  getLyric,
  updateListen,
  getKaraokeWordStyle,
  fillKaraokeWord,
  setBulletText,
  runKaraokeEffect,
  emptyKaraokeWord
};

export default BaiHatService;
