import React from 'react';
import debounce from 'debounce-promise';
import { getAverageRGB } from '../../service/UtilService';

/**
 * Ref: https://www.codehim.com/vanilla-javascript/javascript-audio-waveform-visualizer/
 */
class Waveform extends React.PureComponent {
  constructor(props) {
    super(props);
    this.canvas = React.createRef();
    this.waveformAniFr = null;
  }

  componentDidMount() {
    this.props.getWaveformInstance(this);
    window.onresize = debounce(this.drawWaveform, 300);
  }

  drawWaveform = () => {
    const { flipWaveform, analyser } = this.props;
    if (!analyser) return;
    this.canvas.width = window.innerWidth;
    this.canvas.height = window.innerHeight;
    const canvasContext = this.canvas.getContext('2d');
    const bufferLength = analyser.frequencyBinCount; // = fftSize/2

    // vì có nhiều phần tử cuối của mảng dataArray = 0 nếu dataArray = new Uint8Array(bufferLength)
    const totalBar = parseInt(bufferLength / 1.2);
    const dataArray = new Uint8Array(totalBar);
    const peakArr = Array(totalBar).fill(0);
    const velocityArr = Array(totalBar).fill(0);
    const acceleration = 0.05; // gia tốc của điểm peak khi rơi xuống
    const WIDTH = this.canvas.width;
    const HEIGHT = this.canvas.height;
    const barWidth = parseInt(WIDTH / totalBar);
    const heightLimit = 270; // what does this mean?
    const averageRGB = getAverageRGB();
    let barHeight, barColor, x;

    console.log('bufferLength, totalBar, WIDTH, HEIGHT: ', bufferLength, totalBar, WIDTH, HEIGHT);

    if (window.innerWidth <= 720) return; // not show animation on mobile device

    console.log('Start drawing waveform...');

    // Khi prop flipWaveform thay đổi thì hàm này KHÔNG nhận được giá trị mới, mà phải Refresh lại page
    // Có solution nào đó, chẳng hạn getDerivedStateFromProps hay componentDidUpdate, nhưng lằng nhằng
    // quá nên CHƯA implement được!
    const renderFrame = () => {
      analyser.getByteFrequencyData(dataArray);
      canvasContext.clearRect(0, 0, WIDTH, HEIGHT);

      // for debugging: nếu nó in ra thì animation này vẫn chạy!
      // console.log('dataArray[0]', dataArray[0]);
      for (let i = 0; i < totalBar; i++) {
        // dataArray[i] là 1 số 8bit ko dấu, do đó nó sẽ thuộc khoảng [0,255],
        // nhưng HEIGHT của canvas có thể < 255, do đó nếu cứ để barHeight = dataArray[i]
        // thì sẽ ko đủ chỗ vẽ bar
        // Set min barHeight = 1, vì nếu = 0 sẽ ko thấy hết các bar
        // Note: sau khi add flipWaveform, thì việc nhiều bar = 0 lại bị chuyển về bên trái, haizzz
        // (hiện tại ko flip thì ở bên phải ko còn nhiều bar = 0 nữa, nhưng flip thì lại bị)
        // Sẽ fix sau...
        if (flipWaveform === true) {
          barHeight = 1 + parseInt((dataArray[totalBar - i] / heightLimit) * HEIGHT);
        } else {
          barHeight = 1 + parseInt((dataArray[i] / heightLimit) * HEIGHT);
        }
        // barColor = this.getBarColor(barHeight / HEIGHT, i / totalBar);
        barColor = averageRGB;
        x = (barWidth + 3) * i;

        // draw bar
        canvasContext.fillStyle = barColor;
        canvasContext.fillRect(x, HEIGHT - barHeight, barWidth, barHeight);

        // draw peak of bar
        if (barHeight > peakArr[i]) {
          peakArr[i] = barHeight;
          velocityArr[i] = 0;
        }
        if (peakArr[i] > 0) {
          canvasContext.beginPath();
          canvasContext.strokeStyle = barColor;
          canvasContext.lineWidth = 3;
          canvasContext.moveTo(x, HEIGHT - peakArr[i]);
          canvasContext.lineTo(x + barWidth, HEIGHT - peakArr[i]);
          canvasContext.stroke();
          velocityArr[i] += acceleration;
          peakArr[i] = peakArr[i] - velocityArr[i];
        }
      }

      // Chỉ chạy animation khi audio đang play. Nếu ko check điều kiện này thì cho dù gọi
      // cancelAnimationFrame(this.waveformAniFr) vẫn ko stop được cái animation này!
      if (!this.props.myAudio.paused) this.waveformAniFr = requestAnimationFrame(renderFrame);
    };

    renderFrame();
  };

  /**
   * @param {Number} heightRatio chiều cao của bar so với chiều cao của canvas. heightRatio <= 1
   * @param {Number} barRatio vị trí của bar trong mảng. heightRatio <= 1
   * @returns
   */
  getBarColor = (heightRatio, barRatio) => {
    const r = parseInt(255 * barRatio); // dải màu gradient của mảng bar: blue -> tím hồng
    const g = 100 - 30 * heightRatio; // bar càng cao thì màu càng đậm
    const b = 255;
    return 'rgb(' + r + ',' + g + ',' + b + ')';
  };

  stopWaveform = () => {
    cancelAnimationFrame(this.waveformAniFr);
  };

  render() {
    return <canvas className="canvas-waveform" ref={(node) => (this.canvas = node)}></canvas>;
  }
}

export default Waveform;
