import React, { PureComponent } from 'react';
import Toast from '../../components/Toast/Toast';
import { saveSetting, getSetting } from '../../service/UtilService';
import { scrollPage } from '../../service/scroll';

class Setting extends PureComponent {
  constructor(props) {
    super(props);
    this.state = {
      volume: 100
    };
  }

  onChangeVolume = (e) => {
    const { value } = e.target;
    const volume = value / 100;
    this.setState({ volume: value });
    this.props.myAudio.volume = volume;
    saveSetting('volume', volume);
  };

  componentDidMount() {
    this.props.initSettingsAtPlayer(this.initSettingsAtPlayer);
  }

  /**
   * Init data for html element: lyric_playground, div_result and my_audio
   * Chủ yếu init về css style chứ ko thay đổi state gì của React component
   */
  initSettingsAtPlayer = () => {
    const { myAudio, lyric_playground } = this.props;
    let lyricAlign = getSetting('lyricAlign');
    if (lyricAlign) this.settingLyricAlign(lyricAlign);

    let theme = getSetting('theme');
    if (theme) {
      switch (theme) {
        case 'dark':
          this.changeTheme('dark');
          break;
        case 'light':
          this.changeTheme('light');
          break;
        case 'picture-bg':
          this.changeTheme('picture-bg');
          break;
        default:
          break;
      }
    }

    // changeAudioSpeed
    let speed = getSetting('speed');
    if (speed) myAudio.playbackRate = speed;

    // changeAudioVolume
    let vol = getSetting('volume');
    if (vol) {
      myAudio.volume = vol;
      this.setState({ volume: vol * 100 });
    }

    this.setKaraokeMode(getSetting('karaokeMode'));
    this.setBlurMode(getSetting('blurMode'));
    this.setGiganticLine(getSetting('giganticLine'));
    this.setWordAppear(getSetting('wordAppear'));
    this.setLineAppear(getSetting('lineAppear'));
    this.setTranslate(getSetting('translate'));
    setTimeout(() => {
      scrollPage(lyric_playground, 300);
    }, 10);
  };

  updateSyncLyric = (offset) => {
    this.props.updateSyncLyric(offset);
  };

  settingLyricAlign = (align) => {
    this.props.div_result.style.textAlign = align;
    saveSetting('lyricAlign', align);
  };

  settingTheme = (themeName) => {
    this.changeTheme(themeName);
    saveSetting('theme', themeName);
  };

  /**
   * Note: Class theme sẽ bắt đầu = "lyric-pg-"
   * Do đó những class ko liên quan đến theme KO được bắt đầu = "lyric-pg-"
   */
  changeTheme = (themeName) => {
    const { lyric_playground, div_background } = this.props;

    div_background.style.display = themeName === 'picture-bg' ? '' : 'none';

    lyric_playground.classList.forEach((className) => {
      if (className.includes('lyric-pg-')) lyric_playground.classList.remove(className);
    });
    lyric_playground.classList.add('lyric-pg-' + themeName);
  };

  toggleKaraokeMode = () => {
    if (!this.props.div_result.classList.contains('karaoke-mode')) {
      this.setKaraokeMode(true);
    } else {
      this.setKaraokeMode(false);
    }
  };
  setKaraokeMode = (isKaraokeMode) => {
    const { div_result } = this.props;
    if (isKaraokeMode === true) {
      div_result.classList.add('karaoke-mode');
      div_result.classList.remove('karaoke-off');
      saveSetting('karaokeMode', true);
    } else if (isKaraokeMode === false) {
      div_result.classList.remove('karaoke-mode');
      div_result.classList.add('karaoke-off');
      saveSetting('karaokeMode', false);
    }
  };

  toggleBlurMode = () => {
    const { div_result } = this.props;
    if (!div_result.classList.contains('blur-mode')) {
      this.setBlurMode(true);
    } else {
      this.setBlurMode(false);
    }
  };
  setBlurMode = (isBlurMode) => {
    const { div_result } = this.props;
    if (isBlurMode === true) {
      div_result.classList.add('blur-mode');
      saveSetting('blurMode', true);
    } else if (isBlurMode === false) {
      div_result.classList.remove('blur-mode');
      saveSetting('blurMode', false);
    }
  };

  toggleGiganticLine = () => {
    const { div_result } = this.props;
    if (!div_result.classList.contains('div-res-big-active-line')) {
      this.setGiganticLine(true);
    } else {
      this.setGiganticLine(false);
    }
  };
  setGiganticLine = (isGiganticLine) => {
    const { div_result } = this.props;
    if (isGiganticLine === true) {
      div_result.classList.add('div-res-big-active-line');
      saveSetting('giganticLine', true);
    } else if (isGiganticLine === false) {
      div_result.classList.remove('div-res-big-active-line');
      saveSetting('giganticLine', false);
    }
  };

  flipWaveform = () => {
    let flip = getSetting('flipWaveform');
    if (flip && flip === true) {
      flip = false;
    } else {
      flip = true;
    }
    saveSetting('flipWaveform', flip);
    Toast.info(`Flip waveform = ${flip}, refresh page to take effect!`);

    // Hiện tại việc update qua props này CHƯA implement được, bên component Waveform KHÔNG
    // nhận được giá trị mới sau mỗi lần update, vui lòng F5!!!
    this.props.updateFlipWaveform(flip);
  };

  toggleWordAppear = () => {
    const { div_result } = this.props;
    if (!div_result.classList.contains('word-appear')) {
      this.setWordAppear(true);
    } else {
      this.setWordAppear(false);
    }
  };
  setWordAppear = (isWordAppear) => {
    const { div_result } = this.props;
    if (isWordAppear === true) {
      div_result.classList.add('word-appear');
      saveSetting('wordAppear', true);
    } else if (isWordAppear === false) {
      div_result.classList.remove('word-appear');
      saveSetting('wordAppear', false);
    }
  };

  toggleLineAppear = () => {
    const { div_result } = this.props;
    if (!div_result.classList.contains('line-appear')) {
      this.setLineAppear(true);
    } else {
      this.setLineAppear(false);
    }
  };
  setLineAppear = (isLineAppear) => {
    const { div_result } = this.props;
    if (isLineAppear === true) {
      div_result.classList.add('line-appear');
      saveSetting('lineAppear', true);
    } else if (isLineAppear === false) {
      div_result.classList.remove('line-appear');
      saveSetting('lineAppear', false);
    }
  };

  toggleTranslate = () => {
    const { div_result } = this.props;
    if (!div_result.classList.contains('translate')) {
      this.setTranslate(true);
    } else {
      this.setTranslate(false);
    }
  };
  setTranslate = (isTranslate) => {
    const { div_result } = this.props;
    if (isTranslate === true) {
      div_result.classList.add('translate');
      saveSetting('translate', true);
    } else if (isTranslate === false) {
      div_result.classList.remove('translate');
      saveSetting('translate', false);
    }
  };

  updateSpeed = (offset) => {
    let speed = this.props.myAudio.playbackRate + offset;
    speed = Number(speed.toFixed('1'));
    this.props.myAudio.playbackRate = speed;
    Toast.info('Speed: ' + speed + 'x');
    saveSetting('speed', speed);
  };

  render() {
    const { showSetting, openSetting, isFullscreen } = this.props;
    const { volume } = this.state;
    return (
      <div className="setting_section" style={{ display: isFullscreen ? 'none' : '' }}>
        {!showSetting && (
          <div className="btn_toggle_setting" title="Settings" onClick={openSetting}>
            <i className="fa fa-ellipsis-h"></i>
          </div>
        )}

        {!!showSetting && (
          <div id="setting_wrapper">
            <div>
              <span className="setting-legend">Lyric:&nbsp;</span>
              <i
                className="fa fa-caret-up btn_sync_up"
                title="Sync lyric: -100ms (client only)"
                onClick={() => this.updateSyncLyric(-100)}
              ></i>
              <i
                className="fa fa-caret-down btn_sync_down"
                title="Sync lyric: +100ms (client only)"
                onClick={() => this.updateSyncLyric(100)}
              ></i>
              <i className="setting_separator">|</i>
              <i
                className="fa fa-align-left btn_align btn_align_left"
                title="Lyric left alignment"
                onClick={() => this.settingLyricAlign('left')}
              ></i>
              <i
                className="fa fa-align-center btn_align btn_align_center"
                title="Lyric center"
                onClick={() => this.settingLyricAlign('center')}
              ></i>
              <i
                className="fa fa-align-right btn_align btn_align_right"
                title="Lyric right alignment"
                onClick={() => this.settingLyricAlign('right')}
              ></i>
            </div>

            <div>
              <span className="setting-legend">Theme:&nbsp;</span>
              <i
                className="fa fa-square btn_theme btn_theme_dark"
                title="Dark theme"
                onClick={() => this.settingTheme('dark')}
              ></i>
              <i
                className="fa fa-square btn_theme btn_theme_light"
                title="Light theme"
                onClick={() => this.settingTheme('light')}
              ></i>
              <i
                className="fa fa-picture-o btn_theme btn_theme_picture_bg"
                title="Picture background theme"
                onClick={() => this.settingTheme('picture-bg')}
              ></i>
            </div>

            <div>
              <span className="setting-legend">Mode:&nbsp;</span>
              <i
                className="fa fa-microphone btn_theme btn_karaoke_mode"
                title="Karaoke mode"
                onClick={this.toggleKaraokeMode}
              ></i>
              <i className="fa fa-cloud btn_theme btn_blur_mode" title="Blur mode" onClick={this.toggleBlurMode}></i>
              <i
                className="fa fa-eye-slash btn_theme btn_word_appear"
                title="Word appears when time comes to it"
                onClick={this.toggleWordAppear}
              ></i>
              <i
                className="fa fa-coffee btn_theme"
                title="Word & Line appears when time comes to it"
                onClick={this.toggleLineAppear}
              ></i>
              <i
                className="fa fa-search-plus btn_theme btn_gigantic_line"
                title="Gigantic line"
                onClick={this.toggleGiganticLine}
              ></i>
              <i
                className="fa fa-retweet btn_theme btn_flip_waveform"
                title="Flip waveform horizontally"
                onClick={this.flipWaveform}
              ></i>
              <i
                class="fa fa-language btn_theme btn_translate"
                title="Lyric translation (only .xlrc lyric)"
                onClick={this.toggleTranslate}
              ></i>
            </div>

            <div>
              <span className="setting-legend">Speed:&nbsp;</span>
              <i
                className="fa fa-plus-square btn_theme btn_increase_speed"
                title="Increase speed"
                onClick={() => this.updateSpeed(0.1)}
              ></i>
              <i
                className="fa fa-minus-square btn_theme btn_decrease_speed"
                title="Decrease speed"
                onClick={() => this.updateSpeed(-0.1)}
              ></i>
            </div>

            <div>
              <span className="setting-legend">Volume:&nbsp;</span>
              <input type="range" min="1" max="100" value={volume} className="slider-volume" onChange={this.onChangeVolume} />
            </div>
          </div>
        )}
      </div>
    );
  }
}

export default Setting;
